.why {
    padding-top: 40px;
    padding-bottom: 40px;
    background-image: url(#{$imagesPath}bridgeBackgroundMobile.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    
    &__images {
        display: flex;
        gap: 10px;
    }

    &__images-container {
        width: calc(50% - 5px);
    }

    &__img {
        width: 100%;
        height: auto;
    }

    &__content {
        text-align: center;
    }

    &__title {
        margin: 27px 0 19px 0;
        // margin: 37px 0 19px 0;
        color: $white;
        font-family: $aleo-font;
        font-weight: 400;
        // font-size: 36px;
        // line-height: 40px;
        font-size: 28px;
        line-height: 32px;
        text-transform: capitalize;
    }

    &__text {
        font-weight: 300;
        // font-size: 16px;
        // line-height: 28px;
        font-size: 14px;
        line-height: 26px;
        letter-spacing: 0.1em;
        color: $white;
        margin: 0;

        &_m {
            margin-bottom: 20px;
        }
    }
}

.button {
    margin-top: 24px;
    width: 213px;
    padding: 20px 0 20px 0;
    background-color: $mainColor;
    font-family: $aleo-font;
    font-weight: 400;
    // font-size: 16px;
    // line-height: 20px;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: $white;
}