.book-now {
    padding: 80px 10px;

    &_home {
        background-image: url(#{$imagesPath}houseMobile.png);
        background-repeat: no-repeat;
        background-size: cover;
    }

    &_sunset {
        background-image: url(#{$imagesPath}sunsetMobile.png);
        background-repeat: no-repeat;
        background-size: cover;
    }

    &__content {
        padding: 44px 32px;
        background: rgba(35, 35, 35, 0.9);
    }

    &__title {
        font-weight: 400;
        font-size: 20px;
        line-height: 28px;
        text-transform: uppercase;
        color: $white;
        margin: 16px 0;
    }

    &__text {
        margin: 0;
        font-weight: 300;
        font-size: 20px;
        line-height: 28px;
        letter-spacing: 0.1em;
        color: $white;

        &_fs {
            font-size: 16px;
        }
    }
}