.header {

    &__container {
        padding: 200px 18px 140px 18px;
    }

    &__submit {
        width: 139px;

        &::before {
            left: 24px;
        }
    }

    &__button {
        padding: 16px 24px 16px 52px;
    }
}

.form {
    margin-bottom: 211px;

    &__input {
        letter-spacing: 0.2em;
    }
}

// =========================
// navigation 
.navigation {
    padding: 32px 0;

    &_container {
            width: calc(100% - 36px);
            margin: auto;
            position: relative;
        }
}