.tourist {
    padding: 60px 0 120px 0;
    background-image: url(#{$imagesPath}backgroundTablet.png);

    &__title {
        font-size: 36px;
        line-height: 40px;
    }

    &__content {
        padding: 48px 60px;
    }

    &__caption {
        font-size: 20px;
        line-height: 28px;
    }

    &__text {
        font-size: 16px;
        line-height: 28px;
        letter-spacing: 0.1em;
    }
}