.header {

    &__container {
        background-image: url(#{$imagesPath}headerBackgroundDesktopMin.png);
    }

    &__button {
        cursor: pointer;
    }
}

// navigation 
.navigation {

    &_container {
            width: calc(100% - 64px);
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
}

.burger-line {
    display: none;
}

.menu {
    margin: 0;
}

.link-nav:hover {
    text-shadow: 2px 2px $black;
}