.tourist {
    padding: 60px 0 60px 0;
    background-image: url(#{$imagesPath}backgroundMobile.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &__title {
        text-align: center;
        font-family: $aleo-font;
        font-weight: 400;
        font-size: 28px;
        line-height: 32px;
        text-transform: capitalize;
        color: $white;
        margin: 0;
        margin-bottom: 60px;
    }

    &__description {
        margin: 0;
        font-size: 0;
    }

    &__img {
        width: 100%;
        height: auto;
    }

    &__content {
        padding: 28px 20px;
        background-color: $bgDark;
    }

    &__caption {
        margin: 0;
        margin-bottom: 16px;
        font-weight: 400;

        font-size: 16px;
        line-height: 24px;
        text-transform: uppercase;
        color: $white;
    }

    &__text {
        font-weight: 300;
        font-size: 14px;
        line-height: 26px;
        color: $white;
        position: relative;
        padding-left: -25px;
        text-indent: 25px;

        &::before {
            content: "";
            position: absolute;
            display: block;
            width: 20px;
            height: 20px;
            left: 0;
            top: -6px;
            background-image: url(#{$imagesPath}quote-left.svg);
        }
    }
}