.steps {
    background-color: $bgDark;
    display: flex;
    flex-direction: column;
    align-items: center;
    // padding: 60px 0;
    padding: 40px 0;

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        // margin-bottom: 60px;
        margin-bottom: 40px;
    }

    &__content:last-child {
        margin-bottom: 0;
    }

    &__icon {
        width: 96px;
        height: 96px;
        
    }

    &__text {
        margin-top: 4px;
        margin-bottom: 0;
        color: $white;
        font-family: $aleo-font;
        font-weight: 400;
        // font-size: 36px;
        // line-height: 40px;
        font-size: 30px;
        line-height: 34px;
        text-transform: capitalize;

    }
}