.header {

    &__container {
        position: relative;
        width: 100%;
        padding: 100px 10px 70px 10px;
        text-align: center;
        background-image: url(#{$imagesPath}headerBackgroundMobile.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }

    &__title {
        font-weight: 700;
        font-size: 44px;
        line-height: 48px;
        text-transform: uppercase;
        color: $white;
        margin-top: 0;
        margin-bottom: 8px;
    }

    &__text {
        font-size: 20px;
        line-height: 26px;
        letter-spacing: 0.31em;
        color: $white;
        margin-top: 0;
        margin-bottom: 48px;
    }

    &__link {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.2em;
        color: $white;
    }

    &__submit {
        position: absolute;
        height: 52px;
        width: 103px;
        right: 5px;
        top: 4px;
        cursor: pointer;

        &::before {
            content: "";
            position: absolute;
            width: 16px;
            height: 16px;
            display: block;
            background-image: url(#{$imagesPath}search.svg);
            background-repeat: no-repeat;
            top: 50%;
            transform: translateY(-50%);
            left: 8px;
            z-index: 4;
        }
    }

    &__button {
        background-color: $mainColor;
        position: relative;
        width: 100%;
        padding: 16px 8px 16px 32px;
        color: $white;
        font-family: $aleo-font;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.1em;
        text-transform: uppercase;
    }

    &__link {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.2em;
        color: $white;
        position: relative;
        text-decoration: none;
        display: block;

        &::after {
            content: "";
            position: absolute;
            display: block;
            width: 14px;
            height: 16px;
            background-image: url(#{$imagesPath}chevronDown.svg);
            background-repeat: no-repeat;
            left: 50%;
            transform: translateX(-50%);
            bottom: -20px;
        }
    }
}

.form {
    max-width: 647px;
    width: 100%;
    height: 60px;
    margin: auto;
    margin-bottom: 150px;
    position: relative;

    &__input {
        padding: 20px 0;
        padding-left: 20px;
        width: 100%;
        background-color: $white;
        font-size: 16px;
        line-height: 20px;
        color: #999999;
        font-family: $baseFontFamily;
    }
}

// =========================
// navigation 
.navigation {
    text-align: center;
    padding: 25px 0;
    background-color: $mainColor;
    position: sticky;
    top: 0;
    z-index: 9999;

    &_container {
        width: calc(100% - 20px);
        margin: auto;
        position: relative;
    }

    &__title {
        margin: 0;
        color: $white;
        font-weight: 700;
        font-size: 20px;
        line-height: 20px;
        letter-spacing: 0.1em;
        text-transform: uppercase;
    }

    &__menu {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        height: 24px;
        width: 40px;
    }
}

.burger-line {
    display: block;
    width: 100%;
    height: 2px;
    margin-bottom: 8px;
    background-color: $white;

    &:last-of-type {
        margin-bottom: 0;
    }
}

.menu {
    display: none;
}