.tourist {
    padding: 120px 0 180px 0;
    background-image: url(#{$imagesPath}backgroundDesktopMin.png);

    &__title {
        font-size: 36px;
        line-height: 40px;
    }

    &__content {
        padding: 48px 60px;
    }

    &__caption {
        font-size: 20px;
        line-height: 28px;
    }

    &__text {
        font-size: 16px;
        line-height: 28px;
        letter-spacing: 0.1em;
    }

    &__description {
        display: flex;
    }

    &__img {
        width: 320px;
        height: 100%;
        object-fit: cover;
    }
}
