.footer {
    // padding: 80px 0;
    padding: 60px 0;
    background-color: $black;
    text-align: center;

    &__title {
        font-weight: 700;
        // font-size: 24px;
        // line-height: 28px;
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        color: $white;
        margin: 0;
    }

    &__text {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.2em;
        color: $white;
        margin-top: 8px;
        margin-bottom: 22px;
    }

    &__links {
        margin: auto;
        padding: 0;
        display: flex;
        list-style: none;
        width: 275px;
        justify-content: space-between;
        align-items: center;
    }
}