.header {

    &__container {
        background-image: url(#{$imagesPath}headerBackgroundTablet.png);
    }
}

// navigation 
.navigation {
    padding: 32px 0;

    &_container {
            width: calc(100% - 48px);
            margin: auto;
            position: relative;
        }
}

.burger-line {
    display: none;
}

.menu {
    display: block;
    margin: auto;
    margin-top: 32px;

    &__list {
        list-style: none;
        display: flex;
        margin: auto;
        padding: 0;
        gap: 40px;
        width: fit-content;
    }
}

.link-nav {
    font-family: $aleo-font;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    display: flex;
    align-items: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: $white;
    text-decoration: none;
}