.why {
    padding-top: 124px;
    padding-bottom: 124px;
    background-image: url(#{$imagesPath}bridgeBackgroundDesktopMin.png);

    &__container {
        display: flex;
        align-items: center;
    }

    &__images {
        order: 2;
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 461px;
        margin-left: auto;
        font-size: 0;
    }

    &__images-container {
        width: 350px;
        height: auto;
        object-fit: cover;

        &_pr {
            align-self: flex-start;
        }

        &_pr {
            align-self: flex-end;
        }
    }

    &__content {
        max-width: 550px;
        text-align: left;
        margin-right: 10px;
    }
}
